<template>
  <div class="container-fluid">
    <div class="panel panel-default">
    <div class="panel-heading">
      <h5 class="panel-title" @click="isCollapsed = !isCollapsed">
        <a>Résultats</a>
        <span class="pull-left collapsible-icon bg-ico-tablerow"></span>
        <span class="pull-right fa" :class="{'fui-triangle-up' : isCollapsed,  'fui-triangle-down' : !isCollapsed}"></span>
      </h5>
    </div>


    <div class="panel-collapse" :class="{collapse : isCollapsed}">
      <div class="result-panel-body panel-body">
        <paginator :current-page="this.data.number"
                   :total-pages="this.data.totalPages"
                   :total-items="this.data.totalElements"
                   :itemsPerPage="this.data.numberOfElements"
                   :visiblePages="this.data.size"
                   @page-changed="pageOneChanged">

        </paginator>

        <table class="table-responsive table-drop table-bordered table-striped table-hover resultsTable">
            <thead>
            <tr>
              <th v-for="(value, key) in columns"
                  @click="sortBy(key)"
                  :class="{ active: sortKey == key }">

                {{ value }}
                <span class="arrow" :class="sortOrders[key] > 0 ? 'asc' : 'dsc'">
                  </span>
              </th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="entry in filteredData">
              <template v-for="(value,key) in columns">
                  <td v-if="key === 'statut'" v-status-color="statusColor(entry[key])" class="columnCenter">
                    {{ getStatutLibelleByKey(entry[key]) }}
                  </td>
                  <td class="columnCenter" v-else-if="key === 'dateDebutChgt'">
                    {{entry[key]}}
                  </td>
                  <td class="columnCenter" v-else-if="key === 'dateFinChgt'">
                    {{entry[key]}}
                  </td>
                  <td v-else-if="key === 'famille'">
                    {{ libelleFamilleByKey(entry[key]) }}
                  </td>
                  <td v-else-if="key === 'typeUtilisation'">
                    {{ libelleTypeUtilisationByKey(entry[key]) }}
                  </td>
                  <td v-else-if="isToShowActions(key, entry['statut'])" class="columnCenter">
                      <a @click="showPopupSupprimer(entry)">
                        <span class="glyphicon glyphicon-trash" aria-hidden="true" ></span>
                      </a>
                  </td>

                  <td v-else-if="key === 'nbLignes'" class="columnRight">
                    {{entry[key]}}
                  </td>
                  <td v-else>
                    {{entry[key]}}
                  </td>
              </template>

            </tr>
            </tbody>
        </table>

        <paginator :current-page="this.data.number"
                   :total-pages="this.data.totalPages"
                   :total-items="this.data.totalElements"
                   :itemsPerPage="this.data.numberOfElements"
                   :visiblePages="this.data.size"
                   @page-changed="pageOneChanged">

        </paginator>

      </div>
    </div>
  </div>
    <modal v-if="showModal" @close="showModal = false" @yes="supprimerDonneesFichier()">
      <label class="homer-prompt-q control-label" slot="body">
        Etes-vous sûr de vouloir supprimer les données liées à ce fichier ?
      </label>
    </modal>
  </div>
</template>

<script>

  import Paginator from '../common/Pagination.vue'
  import StatutFichier from '../../data/statutFichier'
  import Modal from '../common/Modal.vue'

  export default {

    props : ['data', 'columns', 'filterKey'],

    data() {
      var sortOrders = {}

        for(let key in this.columns) {
          sortOrders[key] = 1;
        }

      return {
        selectedEntry : {},
        showModal : false,
        isCollapsed: false,
        sortKey: '',
        sortOrders: sortOrders

        /*pagination: {
          currentPage: this.data.number,
          totalPages: this.data.totalPages,
          totalElements : this.data.totalElements,
          numberOfElements : this.data.numberOfElements,
          size : this.data.size
        }*/
      }
    },

    computed : {
      filteredData() {
        var sortKey = this.sortKey
        var filterKey = this.filterKey && this.filterKey.toLowerCase()
        var order = this.sortOrders[sortKey] || 1;
        var data = this.data.content;

        if (filterKey) {
          data = data.filter(function (row) {
            return Object.keys(row).some(function (key) {
              return String(row[key]).toLowerCase().indexOf(filterKey) > -1
            })
          })
        }
        if (sortKey) {
          data = data.slice().sort(function (a, b) {
            a = a[sortKey]
            b = b[sortKey]
            return (a === b ? 0 : a > b ? 1 : -1) * order
          })
        }
        return data
      }

    },

    created() {

      const customActions = {
        deleteFichier : {method : 'GET', url :'app/rest/chargement/deleteFichier{/fileId}'}
      }
      this.resource= this.$resource('', {}, customActions);

    },

    filters: {
      capitalize(str) {
        return str.charAt(0).toUpperCase() + str.slice(1)
      }
    },


    methods: {

      showPopupSupprimer(entry) {
          this.selectedEntry = entry;
          this.showModal = true;
      },

      supprimerDonneesFichier() {
          this.showModal = false;
          console.log('Supprimer le fichier selectionne : ' + this.selectedEntry.id);
          this.resource.deleteFichier({fileId : this.selectedEntry.id})
                .then(response => {
                  return response.json();
                })
                .then(data => {
                  //this.priamGrid.gridData = data;
                  console.log('data = ' + data);

                  this.selectedEntry.statut = data.statut;

                });
      },

      sortBy(key) {
          this.sortKey = key
          this.sortOrders[key] = this.sortOrders[key] * -1
      },

      pageOneChanged (pageNum) {
          this.pagination.currentPage = pageNum;
      },

      findStatusByCode(code) {
        let statut = this.$store.getters.statut;
        return statut.find(function (element) {
            return element.code === code;
          })
      },

      getStatutLibelleByKey (key) {
          var status = this.findStatusByCode(key);

          return status !== undefined && status.libelle;
      },

      statusColor(key) {
          var status = this.findStatusByCode(key);

          return status !== undefined && status.color;
      },

      libelleFamilleByKey(code) {
          var result = this.$store.getters.famille.find(function (element) {
            return element.id === code;
          });
          return result !== undefined && result.value;

      },

      libelleTypeUtilisationByKey(code) {
          var result  = this.$store.getters.typeUtilisation.find(function (element) {
            return element.id === code;
          });
          return result !== undefined && result.value;

      },

      isToShowActions(key, statusCode) {
          if(key === 'action') {
            let element = this.findStatusByCode(statusCode);
              if(element !== undefined && ('CHARGEMENT_KO' === element.code || 'CHARGEMENT_OK' === element.code)) {
                return true;
              }
          }

          return false;
        }

    },

    components : {
        paginator : Paginator,
        modal : Modal
    }

  }
</script>

<style>
  .arrow {
    display: inline-block;
    vertical-align: middle;
    width: 0;
    height: 0;
    margin-left: 5px;
    opacity: 0.66;
  }

  .arrow.asc {
    border-left: 4px solid transparent;
    border-right: 4px solid transparent;
    border-bottom: 4px solid #fff;
  }

  .arrow.dsc {
    border-left: 4px solid transparent;
    border-right: 4px solid transparent;
    border-top: 4px solid #fff;
  }

  .table-striped > tbody > tr:nth-child(odd) > td.statusColor.orange {
    color: black;
    background-color: orange;
  }

  .table-striped > tbody > tr:nth-child(even) > td.statusColor.orange {
    color: black;
    background-color: orange;
  }

  .table-striped > tbody > tr:nth-child(odd) > td.statusColor.green {
    color: black;
    background-color: green;
  }

  .table-striped > tbody > tr:nth-child(even) > td.statusColor.green {
    color: black;
    background-color: green;
  }

  .table-striped > tbody > tr:nth-child(odd) > td.statusColor.red {
    color: black;
    background-color: red;
  }

  .table-striped > tbody > tr:nth-child(even) > td.statusColor.red {
    color: black;
    background-color: red;
  }
</style>



